import React, {useState} from "react"
import img from '../img/img.png'

const Modal = (props) => {
    const [result, setResult] = useState(0)
    const [switcher, setSwitcher] = useState(false)
    const [showResult, setShowResult] = useState(false)
    const [math, setMath] = useState([])
    const modal = props.modal
    const resultRef = React.createRef()
    const open = props.open
    const setOpen = props.setOpen
    const setModal = props.setModal

    return (
        <div className={!modal ? 'modal' : 'modal active'}>

            <div className='title'>Налоговый вычет <span onClick={() => {
                setOpen(false)
                setModal(false)
            }
            }>
                <img className='image' src={img}/>
            </span></div>
            <div className='description'>
                Используйте налоговый вычет чтобы погасить ипотеку досрочно. Размер налогового
                вычета составляет не более 13% от своего официального годового дохода.
            </div>
            <div className='description text'>Ваша зарплата в месяц</div>
            <div>
                <input ref={resultRef} className='input' type="number" placeholder='Введите данные'
                       onChange={() => setResult(parseFloat(resultRef.current.value) * 12 / 100 * 13)}/>
            </div>
            <div className='description text result' onClick={() => {
                setShowResult(true)
                setMath(math.length = 0)
                setMath([...math, result, result, result, result])

            }}>Рассчитать
            </div>
            <div className={showResult ? 'showres active' : 'showres'}>
                <div className='description text'>Итого можете внести в качестве досрочных:</div>

                {math.map((value, key) =>
                    <div key={key}>
                        <input className='checkbox' type='checkbox'/>

                        <label className='description '>
                            <label className='description text'>
                                {value} рублей
                            </label>
                            {' '}в {key + 1}ый год
                        </label>
                    </div>
                )}
            </div>
            <div className=' description text'>Что уменьшаем?
                <span className={!switcher ? 'switcher' : 'switcher active'}
                      onClick={() => setSwitcher(false)}>Платёж</span>
                <span className={!switcher ? 'switcher active' : 'switcher'}
                      onClick={() => setSwitcher(true)}>Срок</span>
            </div>

            <button className='button modal-button'>Добавить</button>
        </div>
    )
}
export default Modal